.Icon-container {
	cursor: pointer;
	display: flex;
	justify-content: center;
	align-items: center;
	height: 48px;
	width: 48px;
	font-size: 24px;
	font-weight: bold;
	/* background-color: #CCC; */
}

.Icon-container-check {
	position: absolute;
	left: 12px;
}

.Icon-container-check--active {
	color: #4caf50;
	font-weight: 900;
}

.Icon-container-delete {
	position: absolute;
	top: -24px;
	right: 0;
	font-weight: 900;
}
/* 
	& En comentarios porque ya no nos sirve
.Icon-container-delete:hover {
	color: red;
} 
*/

.Icon-svg{
	width: 24px;
	height: 24px;
}

.Icon-container-check:hover .Icon-svg{	/* Si se cumple el hover, entonces cambia el del icon */
	fill: green;	/* Por alguna Razon a los SVG se les puede poner la propiedad FILL desde el css tambien */
}
.Icon-container-delete:hover .Icon-svg{	/* Si se cumple el hover, entonces cambia el del icon */
	fill: red;	/* Por alguna Razon a los SVG se les puede poner la propiedad FILL desde el css tambien */
}